.hidden {
  display: none;
}

.visible {
  display: flex !important;
}

.cat-info {
  align-items: flex-start;
  margin-top: 30px;
  display: flex;
}

.cat-info img {
  width: 400px;
  height: auto;
  margin-right: 20px;
  display: block;
}

.cat-info .text-container {
  max-width: 600px;
  flex-direction: column;
  display: flex;
}

.cat-info h2 {
  margin-top: 0;
  margin-bottom: 10px;
}

.cat-info .description, .cat-info .temperament {
  word-wrap: break-word;
  margin-top: 20px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  border: 3px solid rgba(0, 0, 0, 0);
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

